import './App.css';
import NavBar from './components/NavBar';
import News from './components/News';
import LoadingBar from 'react-top-loading-bar';

import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
export default class App extends Component{
  apiKey=process.env.REACT_APP_NEWS_API_KEY;
  state={
    progress:0
  }
  // "fd0efd5122a2478484cc872bbaba1284"
  setProgress=(progress)=>{
    this.setState({progress:progress})
  }
  render(){
  return (
    <div className="App">
      <Router>
      <NavBar></NavBar>
      <LoadingBar
        height={5}
        color='blue'
        progress={this.state.progress}
      />

      <Switch>
        <Route exact path="/"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="general" pageSize={6}  country="in" category="general"></News></Route>
        <Route exact path="/business"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="business" pageSize={6}  country="in" category="business"></News></Route>
        <Route exact path="/entertainment"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="entertainment" pageSize={6}  country="in" category="entertainment"></News></Route>
        <Route exact path="/general"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="general" pageSize={6}  country="in" category="general"></News></Route>
        <Route exact path="/health"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="health" pageSize={6}  country="in" category="health"></News></Route>
        <Route exact path="/science"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="science" pageSize={6}  country="in" category="science"></News></Route>
        <Route exact path="/sports"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="sports" pageSize={6}  country="in" category="sports"></News></Route>
        <Route exact path="/technology"><News setProgress={this.setProgress} apiKey={this.apiKey}  key="technology" pageSize={6}  country="in" category="technology"></News></Route>
      </Switch>
      </Router>
    </div>
    // pageSize={6}  country="in" category="science"
    // if we pass props at both the places then app.js is given preference
  );
}
}


