import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'
import InfiniteScroll from 'react-infinite-scroll-component';

export class News extends Component {

    static defaultProps={
        country:"in",
        pageSize:6,
        category:"general"
    }

    static propTypes={
        country:PropTypes.string,
        pageSize:PropTypes.number,
        category:PropTypes.string
    }
    //either you can pass these prop types from here or you van also pass these propp types from app.js.....they are also commented
      constructor()
      {
          super();
          this.state={
              articles:[],
              loading:true,
              page:1,
              totalResults:0
          }
      }

      async updateNews(page)
      {
          this.props.setProgress(30);
        this.setState({loading:true})
        const url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=fd0efd5122a2478484cc872bbaba1284&page=${this.state.page}&pageSize=${this.props.pageSize}`;
        let data=await fetch(url);
        let parsedData=await data.json();
        console.log(parsedData);
        this.setState({
          articles:parsedData.articles,
          totalResults:parsedData.totalResults,
          loading:false
        })
          this.props.setProgress(100);
      }
      
      async componentDidMount()
      {
          this.updateNews();
      }
    //  handlePreviousClick=async()=>{
    //     this.setState({page:this.state.page-1});
    //     this.updateNews();
    // }
    //  handleNextClick=async()=>{
    //         this.setState({page:this.state.page+1})
    //         this.updateNews();
    // }
    fetchMoreData = async() => {
        this.setState({page:this.state.page+1})
        const url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=fd0efd5122a2478484cc872bbaba1284&page=${this.state.page}&pageSize=${this.props.pageSize}`;
        let data=await fetch(url);
        let parsedData=await data.json();
        console.log(parsedData);
        this.setState({
          articles:this.state.articles.concat(parsedData.articles),
          totalResults:parsedData.totalResults,
          loading:false})
      };
      
    render() {

        return (
                <>
                    <h3 className="text-center" style={{margin:'35px 0px',textDecoration:'underline' }}>Top News Headlines in {this.props.category}</h3>
                    {this.state.loading && <Spinner/>}
                    {/* this line means that the spinner will be shown only when loading is true */}
                    <InfiniteScroll
                        dataLength={this.state.articles.length}
                        next={this.fetchMoreData}
                        hasMore={this.state.articles.length!==this.state.totalResults}
                        loader={<Spinner/>}
                        >
                            <div className="container">
                        <div className="row " >
                            {/* {!this.state.loading && this.state.articles.map((element)=>{ */}
                            {/* no need to use this.state.loading as we are using infinite scroll so we need our existing news should be concatenated with the upcoming one*/}
                            {this.state.articles.map((element)=>{
                                return <div className="col-md-4" key={element.url}>
                                <NewsItem publishedAt={element.publishedAt} author={element.author} title={(element.title==null)?element.title:element.title.slice(0,44)} description={(element.description==null)?element.description:element.description=element.description.slice(0,80)+"...."} imageUrl={(element.urlToImage==null)?"https://media.istockphoto.com/photos/blue-world-map-background-global-business-news-and-media-finance-and-picture-id1300113567?b=1&k=20&m=1300113567&s=170667a&w=0&h=Rz604QF4tSjJUGQJTh_rwNOeBi2Ob9vpzSzpNKIkUtY=":element.urlToImage} newsUrl={element.url}></NewsItem>
                            </div>
                            })}
                        </div>
                        </div>
                    </InfiniteScroll>
                </>
                // {/* <div className="container d-flex justify-content-between mb-3">
                // <button type="button" disabled={this.state.page<=1?true:false} className="btn btn-dark" onClick={this.handlePreviousClick}> &laquo; Previous</button>
                // <button type="button" disabled={(this.state.page+1> Math.ceil(this.state.totalResults/this.props.pageSize))?true:false}className="btn btn-dark" onClick={this.handleNextClick}>Next &raquo;</button>
                // </div> */}
        )
    }
}

export default News
